// Доступ до списку з зображеннями
const galleryContainer = document.querySelector('.gallery');

// Функція для динамічного створення розмітки галереї зображень
function createMarkup(arr) {
  return arr
    .map(
      item => `
    <li class="gallery-item">
	  <a class="gallery-link" href="${item.original}">
		<img 
			class="gallery-image" 
			src="${item.preview}" 
			alt="${item.description}" 
			/>
	  </a>
    </li>
        `
    )
    .join('');
}

// Додаємо створену розмітку на сторінку
galleryContainer.insertAdjacentHTML('beforeend', createMarkup(images));

// Ініціалізуємо бібліотеку SimpleLightbox
new SimpleLightbox('.gallery a', {
  captions: true,
  captionSelector: 'img',
  captionType: 'attr',
  captionsData: 'alt',
  captionDelay: 250,
});
